.section {
	width: 80vw;
	margin: 5rem auto;
	max-width: 950px;
	/* width: 100%; */
}

@media screen and (min-width: 992px) {
	.section {
		width: 85vw;
	}
}

.section__center {
	height: 100px;
	text-align: center;
	display: flex;
	overflow: hidden;
	background-repeat: no-repeat;
}

article {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 50%;
	opacity: 0;
	transition: all 0.3s linear;
}

article.activeSlide {
	opacity: 1;
	transform: translateX(0);
}
article.lastSlide {
	transform: translateX(-100%);
}
article.nextSlide {
	transform: translateX(100%);
}

.banner__img {
	margin-top: 120px;
	margin-left: 10px;
	margin-right: -10px;
	width: 100%;
	
	z-index: -2;
	margin-bottom: -240px;
	mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));

}

.prev,
.next {
	position: absolute;
	top: 200px;
	margin-left: 10px;
	color: black;
	width: 1.25rem;
	height: 1.25rem;
	display: grid;
	place-items: center;
	cursor: pointer;
}

.prev {
	left: 0;
}
.next {
	right: 0;
}
@media (min-width: 800px) {
	.text {
		max-width: 35em;
	}
	.prev,
	.next {
		width: 2rem;
		height: 2rem;
		font-size: 2.5rem;
	}
}